import React, { Component } from 'react';

export default class NotMatch extends Component {


    render() {

        return(
            <div>
                <div></div>
                <div onClick={()=>{
                    this.props.history.goBack();
                }}></div>
                <div onClick={()=>{
                    this.props.history.replace('/');
                }}></div>
            </div>
        );
    }
}


